<template>
  
  <div v-if="isShow" class="frame">
    <div class="frame__header" style="background: #eee; padding: 1rem; margin: 1rem">
        <div class="frame__title u-text-center" style="font-size: 28px">discord</div>
    </div>
    <div class="frame__body bg-gray-200 u-round-xs p-4 u-shadow-lg u-text-center" style="background: #eee; padding: 1rem; margin: 1rem">
        <div class="frame__title u-text-center" style="font-size: 28px">webhook</div>
        <div class="row level">
            <div class="col-xs-3 level-item">
                <p class="m-0">url:</p>
            </div>
            <div class="col-xs-9 level-item"><input v-model=url type="url"></div>
        </div>
        <div @click="dcurl" class="btn btn-animated bg-orange-500 text-light" style="font-size: 28px"></div>
    </div> 
</div>
      <div v-if="loggon" class="spinner u-absolute u-left-40p u-top-50p u-right-50p u-bottom-0">
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      <div></div>
      </div>
</template>
<script>
import mqtt from 'mqtt'
// import the library
import { createToast } from 'mosha-vue-toastify';
// import the styling for the toast
import 'mosha-vue-toastify/dist/style.css'
export default {
  name: 'MQTTMode',
  created() {
    // this.doUnSubscribe()
    this.destroyConnection()
    this.createConnection()
    this.doSubscribe()
    this.doPublish()

  },
  data() {
    return {
      timer:null,
      time: 10,
      url:"",
      isShow: false,
      loggon:true,
      code:this.$route.query.code,
      connection: {
        host: 'LWCJacky.myDs.me',
        port: 8084,
        endpoint: '/mqtt',
        clean: true, // 
        connectTimeout: 4000, // 
        reconnectPeriod: 4000, // 
        // 
        clientId: "",
        username: 'restream',
        password: 'flCAf&fi?*dUjO_udoqO2a-toga@lneTOtri0HIVlcHOrUchUVOcawLfrEf#RUfr',
      },
      subscription: {
        topic: 'restream/online',
        qos: 2,
        receiveNews:{'code':""}
      },
      
      publish: {
        topic: 'restream/publish',
        qos: 2,
        receiveNews:{'code':this.$route.query.code,'mode':1},
      },
      
      qosList: [
        { label: 0, value: 0 },
        { label: 1, value: 1 },
        { label: 2, value: 2 },
      ],
      client: {
        connected: false,
      },
      subscribeSuccess: false,
    }
  },
  mounted () {
    this.connection.clientId=this.randomString(32)
    this.timer=setInterval(this.timeout, 1000);
  },
  methods: {
    timeout(){//
      this.time--
      console.log(this.time)
      if(this.time==0){
        clearInterval(this.timer)
        this.toast("!",2);
        this.$router.push({name:"Home"}); 
      }
    },
    toast(text,mode=1){//
      if(mode===1){
        createToast(text,
        {
        position: 'bottom-right',
        timeout: 3000,
        showIcon: false,
        transition: 'slide',
        hideProgressBar: false,
        type: 'default',
        swipeClose: true,
        showCloseButton: true,
        })
      }else if(mode===2){
        createToast(text,
        {
        position: 'bottom-right',
        timeout: 3000,
        showIcon: 'true',
        transition: 'slide',
        hideProgressBar: 'false',
        type: 'danger',
        swipeClose: 'true',
        showCloseButton: 'true',
        })
        }
        else if(mode===3){
          createToast(text,{
          position: 'bottom-right',
          timeout: 3000,
          showIcon: true,
          transition: 'slide',
          hideProgressBar: false,
          type: 'success',
          swipeClose: true,
          showCloseButton: true,
          })
        }
    },
    randomString(e) { 
      e = e || 32;
      var t = "ABCDEFGHJKMNPQRSTWXYZabcdefhijkmnprstwxyz2345678",a = t.length,n = "";
      for (var i = 0; i < e; i++) n += t.charAt(Math.floor(Math.random() * a));
      return n
    },
    // 
    createConnection() {
      // , 
      // ws  WebSocket 
      // wss  WebSocket 
      // mqtt  TCP 
      // mqtts  TCP 
      // wxs 
      // alis 
      const { host, port, endpoint, ...options } = this.connection
      const connectUrl = `wss://${host}:${port}${endpoint}`
      try {
        this.client = mqtt.connect(connectUrl, options)
      } catch (error) {
        console.log('mqtt.connect error', error)
      }
      this.client.on('connect', () => {
        console.log('Connection succeeded!')
      })
      this.client.on('error', error => {
        console.log('Connection failed', error)
      })
      this.client.on('message', (topic, message) => {
        this.subscription.receiveNews = JSON.parse(message)
        if(this.subscription.receiveNews.code===this.publish.receiveNews.code){
          if(this.subscription.receiveNews.msg==="login"){
            console.log("pass")
            this.isShow= true
            this.loggon=false
            this.toast("")
            clearInterval(this.timer)
          }
          else if(this.subscription.receiveNews.msg==="url_error"){
            this.toast("!",2)
          }else if(this.subscription.receiveNews.msg==="url_pass"){
            this.toast("",3)
            this.$router.push({name:"Home"});
          }
          
        }
        console.log(`Received message ${this.subscription.receiveNews.code} from topic ${topic}`)
      })
    },
    dcurl(){
      this.publish.receiveNews={'code':this.$route.query.code,'url':this.url,'mode':2}
      this.doPublish();
    },
    // 
    doSubscribe() {
      const { topic, qos } = this.subscription
      this.client.subscribe(topic, { qos }, (error, res) => {
        if (error) {
          console.log('Subscribe to topics error', error)
          return
        }
        this.subscribeSuccess = true
        console.log('Subscribe to topics res', res)
      })
    },
    // 
    doUnSubscribe() {
      const { topic } = this.subscription
      this.client.unsubscribe(topic, error => {
        if (error) {
          console.log('Unsubscribe error', error)
        }
      })
    },
    // 
    doPublish() {
      const { topic, qos, receiveNews } = this.publish
      this.client.publish(topic,  JSON.stringify(receiveNews), qos, error => {
        if (error) {
          console.log('Publish error', error)
        }
      })
    },
    // 
    destroyConnection() {
      if (this.client.connected) {
        try {
          this.client.end()
          this.client = {
            connected: false,
          }
          console.log('Successfully disconnected!')
        } catch (error) {
          console.log('Disconnect failed', error.toString())
        }
      }
    },
  
  }
}
</script>
<style scoped>
.spinner {
 width: 44px;
 height: 44px;
 animation: spinner-y0fdc1 2s infinite ease;
 transform-style: preserve-3d;
}

.spinner > div {
 background-color: rgba(0,77,255,0.2);
 height: 100%;
 position: absolute;
 width: 100%;
 border: 2px solid #004dff;
}

.spinner div:nth-of-type(1) {
 transform: translateZ(-22px) rotateY(180deg);
}

.spinner div:nth-of-type(2) {
 transform: rotateY(-270deg) translateX(50%);
 transform-origin: top right;
}

.spinner div:nth-of-type(3) {
 transform: rotateY(270deg) translateX(-50%);
 transform-origin: center left;
}

.spinner div:nth-of-type(4) {
 transform: rotateX(90deg) translateY(-50%);
 transform-origin: top center;
}

.spinner div:nth-of-type(5) {
 transform: rotateX(-90deg) translateY(50%);
 transform-origin: bottom center;
}

.spinner div:nth-of-type(6) {
 transform: translateZ(22px);
}

@keyframes spinner-y0fdc1 {
 0% {
  transform: rotate(45deg) rotateX(-25deg) rotateY(25deg);
 }

 50% {
  transform: rotate(45deg) rotateX(-385deg) rotateY(25deg);
 }

 100% {
  transform: rotate(45deg) rotateX(-385deg) rotateY(385deg);
 }
}
</style>